@charset "UTF-8";
html,
body {
  scroll-behavior: auto !important;
}

.theme-editor {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  max-height: 100vh;
  text-align: start;
}
.theme-editor _-- {
  background-color: rgb(201, 190, 190);
}
.theme-editor button, .theme-editor input {
  background: white;
  margin-right: 2px;
  border: 1px solid black;
  padding: 4px 7px;
  border-radius: 6px;
  font-size: 16px;
}
.theme-editor button, .theme-editor input:not([type=checkbox], [type=radio]), .theme-editor select, .theme-editor label:has(> [type=checkbox]) {
  min-width: 44px;
  min-height: 44px;
}
.theme-editor h4 {
  font-size: 18px !important;
  margin-top: 0 !important;
  margin-bottom: 8px;
}
.theme-editor h4 _-- {
  white-space: break-spaces;
}
.theme-editor h5 {
  font-size: 16px !important;
  margin-top: 0 !important;
}
.theme-editor :focus {
  outline: 3px solid rgb(40, 155, 255) !important;
}

.theme-length-controls {
  clear: both;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.theme-length-control {
  display: inline-flex;
}
.theme-length-control input _-- {
  margin-right: 5px;
  text-align: right;
  min-width: 40px;
  max-width: 120px;
}
.theme-length-control > * {
  display: inline;
  flex-shrink: 2;
}

.scroll-in-view {
  opacity: 0.7;
}
.scroll-in-view:hover {
  opacity: 1;
}

.theme-editor *:not(.font-button):not(.monospace-code), .responsive-size-controls * {
  font-family: Roboto, sans-serif !important;
  color: black;
  line-height: initial !important;
}

.responsive-size-controls input {
  width: 90px;
}

.components-range-control__track {
  color: var(--theme-editor--acent-color, #007cba);
}

.force-cursor, .force-cursor * {
  cursor: help !important;
}

.responsive-frame-container {
  transform-origin: 0 0;
}
.responsive-frame-container iframe {
  background-color: white;
}

.responsive-frame-container::-webkit-scrollbar {
  color: transparent;
  background-color: transparent;
}

.responsive-frame-container::-webkit-resizer {
  background-color: rgb(179, 162, 162);
}

.responsive-screen-options {
  z-index: 2;
  list-style-type: none;
}

.server-theme-list {
  flex-shrink: 0;
}

.server-theme {
  background: white;
  text-align: center;
  font-size: 14px;
  height: 21px;
  margin-bottom: 4px;
  clear: both;
}
.server-theme button {
  font-size: 14px;
  padding: 2px;
}

.server-theme-current _-- {
  background: rgba(246, 246, 89, 0.63);
}

.theme-editor ul {
  margin-left: 0;
  padding-left: 0;
}

.var-control li {
  list-style-type: none;
}

.group-list li:before {
  content: none !important;
}

.group-list, .server-theme-list {
  padding: 0;
  margin-bottom: 0;
}
.group-list --theme-editor--lists--, .server-theme-list --theme-editor--lists-- {
  min-width: 320px;
}
@media screen and (min-width: 800px) {
  .group-list --theme-editor--lists--, .server-theme-list --theme-editor--lists-- {
    min-width: 360px;
  }
}
.group-list li, .server-theme-list li {
  list-style-type: none;
}
.group-list::-webkit-scrollbar, .server-theme-list::-webkit-scrollbar {
  width: 9px;
}
.group-list::-webkit-scrollbar-track, .server-theme-list::-webkit-scrollbar-track {
  border-radius: 5px;
  background: rgba(140, 140, 140, 0.2);
}
.group-list::-webkit-scrollbar-thumb, .server-theme-list::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: rgba(140, 140, 140, 0.4);
}
.group-list::-webkit-scrollbar-thumb:hover, .server-theme-list::-webkit-scrollbar-thumb:hover {
  background: rgba(140, 140, 140, 0.5);
}
.group-list::-webkit-scrollbar-thumb:window-inactive, .server-theme-list::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(140, 140, 140, 0.05);
}

.theme-editor-menu {
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
}

@keyframes pulse {
  70% {
    outline-offset: 4px;
  }
}
.theme-editor-highlight {
  outline: 4px solid rgb(105, 82, 236) !important;
  outline-offset: -2px;
  animation: pulse 0.2s;
}

.shadow-picker {
  width: 100% !important;
  background: transparent !important;
}

.var-group {
  min-height: 49px;
  background: white !important;
  border: 1px solid black !important;
  margin-bottom: 3px !important;
  border-radius: 7px !important;
  padding: 2px 7px 7px !important;
}
.var-group h4, .var-group h5 {
  clear: none;
  margin-bottom: 2px;
}

.var-control {
  clear: both;
  min-height: 57px;
  border: 1px solid black !important;
  margin-bottom: 3px !important;
  border-radius: 7px !important;
  padding: 0 7px 7px !important;
}
.var-control _-- {
  background-color: #e2e2e2;
}
.var-control h5 {
  clear: none;
  margin-bottom: 2px;
}

.var-control-in-theme _-- {
  background-color: #fcfafa;
}

.var-control-no-match-screen {
  opacity: 0.3;
}
.var-control-no-match-screen:hover {
  opacity: 0.6;
}

.var-control-property _-- {
  color: inherit;
  background-color: transparent;
  padding: 0 2px;
  border-radius: 4px;
}

.variable-screen-switcher {
  margin: 0 !important;
  opacity: 0.5;
  list-style-type: none;
  position: absolute;
  top: 21px;
  right: 7px;
}
.var-control:hover .variable-screen-switcher, .var-control:focus-within .variable-screen-switcher {
  opacity: 1;
}
.variable-screen-switcher li {
  display: inline-block;
  margin-left: 4px;
  vertical-align: top;
}
.variable-screen-switcher button {
  background: lightgray;
  border: 1px solid darkgray;
  border-radius: 3px;
  padding: 0;
  width: 45px;
  height: 42px;
  vertical-align: center;
}

.variable-screen-switcher-screen {
  display: block;
  padding: 0;
  margin: 0 auto;
  box-sizing: content-box;
  background: aquamarine;
  border: 1px solid gray;
  border-radius: 1px;
}

.usages-collapsed {
  max-height: 32px;
  max-width: 190px;
  overflow-y: hidden;
  overflow-x: hidden;
}

.usages-open {
  clear: both;
}

div[id^=font-picker] ul {
  position: static !important;
}

.usages-wrapper {
  position: relative;
  max-height: 32px;
  overflow: hidden;
}

.usages-wrapper:hover {
  overflow: visible;
}

.font-weight-control {
  display: flex;
  justify-content: space-between;
}

.render-info {
  --bg-opacity: calc(var(--current-renders, 1) / max(var(--max-tracked-renders, 1), 2));
  background-color: rgba(255, 240, 17, var(--bg-opacity));
}

.theme-editor .history-button {
  font-size: 24px;
  min-width: 64px;
  min-height: 64px;
}

.content {
  min-height: 400px;
}

.area-switcher {
  max-width: 120px;
}

.area {
  z-index: 10000;
  display: flex;
  gap: 8px;
  align-items: flex-start;
}
.area > * {
  padding: 2px;
  border: 1px solid rgb(179, 162, 162);
  border-radius: 2px;
  flex-shrink: 0;
}
.area > :hover {
  border-color: black;
}
.is-dragging .area > :hover {
  border-color: transparent;
}

#area-left, #area-right {
  scrollbar-gutter: stable;
  overflow-y: auto;
  overflow-x: visible;
  flex-direction: column;
  flex-shrink: 0;
  align-items: stretch;
}
#area-left > .movable-element > *, #area-right > .movable-element > * {
  width: 400px;
}
@media screen and (min-height: 350px) {
  #area-left, #area-right {
    max-height: calc(100vh - var(--top-area--height, 60px) - var(--bottom-area--height, 60px));
  }
}
#area-left .area-dropzone, #area-right .area-dropzone {
  transition: width, height 0.2s ease-in;
}
#area-left .area-switcher, #area-right .area-switcher {
  top: 0;
  right: 0;
}

#area-left-inner {
  flex-direction: column;
}

#drawer .area-switcher {
  right: 0;
  top: 0;
  position: static !important;
}

#area-top, #area-bottom, #area-top-reverse, #area-bottom-reverse {
  z-index: 10001;
}
#area-top > *, #area-bottom > *, #area-top-reverse > *, #area-bottom-reverse > * {
  background-color: rgb(201, 190, 190);
}
#area-top .area-dropzone, #area-bottom .area-dropzone, #area-top-reverse .area-dropzone, #area-bottom-reverse .area-dropzone {
  transition: width, height 0.2s ease-in;
}
#area-top label, #area-bottom label, #area-top-reverse label, #area-bottom-reverse label {
  display: inline-block;
}

#area-bottom, #area-bottom-reverse {
  flex-grow: 1;
}

#area-top .area-switcher {
  bottom: 0;
  left: 0;
}

#area-bottom {
  position: fixed !important;
  bottom: 0;
  left: 0;
  z-index: 1000000;
  align-items: flex-end;
}
#area-bottom .area-switcher {
  z-index: 1000001;
  top: 4px;
  right: 0;
}
#area-bottom > * {
  max-height: 50vh;
  max-width: 50vw;
  overflow-y: auto;
}

.dropzone {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(33, 52, 26, 0.2);
}

.area-dropzone {
  width: 0;
  height: 0;
  align-self: flex-end;
  visibility: hidden;
}
.is-dragging .area-dropzone {
  visibility: visible;
  flex-shrink: 1 !important;
  flex-grow: 1 !important;
  transition-timing-function: ease-out;
  width: auto;
  height: auto;
  align-self: stretch;
}
.is-dragging .area-dropzone:only-child {
  flex: 0;
}

.is-dragging #area-top .area-dropzone, .is-dragging #area-bottom .area-dropzone {
  min-width: 20px;
}
.is-dragging #area-top .area-dropzone:only-child, .is-dragging #area-bottom .area-dropzone:only-child {
  height: 30px;
}
.is-dragging #area-top .drag-hovered, .is-dragging #area-bottom .drag-hovered {
  border-left: 4px solid yellow;
  min-width: 120px;
}
.is-dragging #area-top-reverse .area-dropzone, .is-dragging #area-bottom-reverse .area-dropzone {
  min-width: 20px;
}
.is-dragging #area-top-reverse .area-dropzone:only-child, .is-dragging #area-bottom-reverse .area-dropzone:only-child {
  height: 30px;
}
.is-dragging #area-top-reverse .drag-hovered, .is-dragging #area-bottom-reverse .drag-hovered {
  border-right: 4px solid yellow;
  min-width: 120px;
}
.is-dragging #area-left .area-dropzone:only-child, .is-dragging #area-right .area-dropzone:only-child, .is-dragging #area-left-inner .area-dropzone:only-child {
  width: 60px;
}
.is-dragging #area-left .drag-hovered, .is-dragging #area-right .drag-hovered, .is-dragging #area-left-inner .drag-hovered {
  border-top: 4px solid yellow !important;
}
.is-dragging #drawer .drag-hovered {
  border-bottom: 4px solid yellow !important;
}

.movable-container {
  display: flex;
  gap: 8px;
  flex-direction: column;
  align-items: stretch;
  justify-content: stretch;
  z-index: 10000;
  position: absolute;
  width: 100%;
  height: 100%;
}

.is-dragged {
  overflow: hidden;
}
.is-dragged > * {
  width: 0 !important;
  min-width: 0 !important;
  height: 0 !important;
  min-height: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  overflow: hidden !important;
}

.movable-element {
  min-width: 0;
  min-height: 0;
  transition: min-width, min-height 0.2s ease;
  transition-delay: 0.1s;
}

.is-dragging #area-left, .is-dragging #area-right {
  min-width: 140px;
}
.is-dragging #area-left .movable-element:not(.is-dragged), .is-dragging #area-right .movable-element:not(.is-dragged) {
  min-height: 20px;
}
.is-dragging #area-top, .is-dragging #area-bottom {
  transition: min-height;
  transition-delay: 0.1s;
}
.is-dragging #area-top .movable-element:not(.is-dragged), .is-dragging #area-bottom .movable-element:not(.is-dragged) {
  min-width: 20px;
}

#drawer-wrapper {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 10000;
}

#drawer {
  margin-top: 24px;
  position: absolute !important;
  bottom: 40px;
  right: 0;
  display: flex;
  flex-direction: column-reverse;
  transition: min-height 1s ease;
  transition-delay: 0.5s;
  align-self: flex-end;
  max-height: 70vh;
  overflow-y: auto;
}
#drawer[data-open=true] {
  background: rgb(179, 162, 162);
  border: 2px solid black;
  border-radius: 4px;
  padding: 8px;
}

.is-dragging #drawer {
  align-self: stretch;
  border: 1px solid grey;
  min-height: 200px;
  min-width: 200px;
}
.is-dragging #drawer .area-dropzone {
  min-height: 32px;
  min-width: 120px;
}

#drawer-opener {
  min-height: 40px;
  flex-grow: 0 !important;
  font-size: 22px;
  text-align: center;
  background: rgb(179, 162, 162);
}

.is-compact {
  overflow: visible !important;
}

.movable-element-collapse {
  position: absolute;
  padding-top: 2px;
  min-width: 24px !important;
  min-height: 24px !important;
  top: 0;
  right: 0;
}
#area-bottom .movable-element-collapse, #area-bottom-reverse .movable-element-collapse {
  top: auto;
  bottom: 0;
}

.monospace-code {
  display: inline-block;
  white-space: pre-wrap;
  font-family: monospace !important;
}
.monospace-code _-- {
  font-size: 16px;
  background: white;
  border: 1px solid gray;
  border-radius: 4px;
  padding: 4px !important;
}
.monospace-code .var-control-property _-- {
  margin-left: 16px;
  color: purple !important;
}

.var-control:not([data-nesting-level="0"]) > div:first-child:before {
  vertical-align: top;
  float: left;
  margin-top: -10px;
}
.var-control:not([data-nesting-level="0"]) > div:first-child:before _-- {
  content: "=" !important;
  font-size: 36px;
}

.var-control .var-control {
  border-radius: 2px !important;
  padding: 0 !important;
  border: none !important;
  padding-bottom: 12px !important;
}

.connected-list {
  display: flex;
  flex-direction: column-reverse;
}
.connected-list ::marker {
  font-size: 38px;
  line-height: 20px;
  margin-right: 0;
}
.connected-list > li {
  list-style: disc !important;
  list-style-position: inside !important;
  position: relative;
  border-radius: 8px;
  margin-bottom: 0;
  padding-top: 2px;
  padding-bottom: 0;
}
.connected-list > li > * {
  margin-left: 12px;
}
.connected-list > li:before {
  content: "";
  background: black;
  width: 3px;
  position: absolute;
  top: 0px;
  left: 4px;
  bottom: 0px;
}
.connected-list > li:first-child:after {
  content: "";
  background: black;
  position: absolute;
  left: 4px;
  bottom: 0px;
  height: 3px;
  right: 0px;
}
.connected-list > li:last-child:before {
  top: 24px;
}

.history-actions li {
  list-style-type: none;
  border: 1px dashed black;
  border-radius: 12px;
  padding: 2px;
}
.history-actions pre {
  margin-bottom: 0;
}

.history > ul > li:not(:hover) button:not(.pinned-here) {
  visibility: hidden;
}

.pin {
  display: inline-block;
  transition: rotate 0.1s ease-in, translate 0.12s;
  transition-delay: 0.07s, 0s;
}

.pinned-here .pin {
  rotate: -45deg;
  translate: 0 9px;
  transition-delay: 0s, 0.07s;
}

.pinned-initial .pin {
  rotate: 45deg;
  translate: -22px 2px;
  transition-delay: 0s, 0.07s;
}

.pin-latest .pin {
  rotate: -90deg;
}

.pinned-latest .pin {
  rotate: -135deg;
  translate: 22px 3px;
  transition-delay: 0s, 0.07s;
}

button {
  width: auto;
  display: inline-block;
}

.svg-inspect-wrapper {
  overflow: hidden !important;
  align-self: flex-start;
}
.svg-inspect-wrapper > svg {
  max-width: 100%;
}

.history-open-groups label {
  width: 100%;
}
.history-open-groups label span {
  display: inline-block;
  text-align: center;
  padding: 8px;
  width: 95%;
  border: 1px solid grey;
}
.history-open-groups input[type=checkbox] {
  display: none;
}

.history-actions > li > * {
  vertical-align: top;
}

.flex-column {
  flex-direction: column !important;
}

@keyframes blink {
  0% {
    box-shadow: 0 0 0 0 rgba(124, 50, 177, 0.708);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(76, 0, 130, 0.406);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(76, 0, 130, 0.721);
  }
}
:has(> .tutorial-content) {
  animation: 0.7s linear 0s infinite blink;
}

.area:has(.tutorial-content) {
  overflow: visible !important;
}

.tutorial-content {
  z-index: 1000000;
  min-width: 320px;
}
:has(.tutorial-content) {
  overflow: visible !important;
}
#drawer .tutorial-content {
  bottom: 100%;
  right: 0;
}
#area-left .tutorial-content, #area-left-inner .tutorial-content {
  left: 100%;
}

li[data-done] {
  margin-top: 16px;
  list-style-type: none;
}

li[data-done=false]:before {
  vertical-align: bottom;
}

li[data-done]:before {
  content: "...";
  color: grey;
  min-width: 36px;
  margin-right: 4px;
  font-size: 24px;
  font-weight: 900;
}

li[data-done=true]:before {
  content: "✓";
  color: green;
}

button:disabled {
  filter: opacity(0.5);
}

.hide-important {
  visibility: hidden !important;
}

.hide-scrollbars {
  overflow-y: scroll !important;
  text-rendering: optimizeSpeed;
}

.chrome-picker {
  user-select: none;
}